@import url("https://fonts.googleapis.com/css2?family=Amaranth:wght@400;700&family=Open+Sans:wght@300;400;600;700;800&display=swap");

.footer-lecchain {
  padding: 15px 15px;
  font-family: "Open Sans";
  color: white;
  font-weight: bold;
  background-color: black;
  display: flex;
  justify-content: center;
}

.left-footer-lecchain {
  width: 500px;
}

.right-footer-lecchain {
  width: 500px;
  display: flex;
  padding: 0px 75px;
  justify-content: space-around;
  align-items: center;
}

.right-footer-lecchain img {
  border-radius: 10px;
}

.link-footer-lecchain {
  text-decoration: none;
  color: rgb(123, 123, 123);
}

.link-footer-lecchain:hover {
  color: #8ce2ff;
}